  function handleIconUploading(event, action) {
    if (action === "add") {
      var extension = event.target.files[0].name.substring(
        event.target.files[0].name.length - 3,
        event.target.files[0].name.length
      );
      if (extension === "svg") {
        var jsonForIcons = jsonIcons;
        jsonForIcons = { name: "", file: "" };
        jsonForIcons["name"] = event.target.files[0].name;
        jsonForIcons["file"] = event.target.files[0];
        props.selectedControl.style["icon"] = { name: "", file: "" };
        props.selectedControl.style["icon"]["name"] =
          event.target.files[0].name;
        props.selectedControl.style["icon"]["file"] = event.target.files[0];
        setjsonForIcons(jsonForIcons);
        setIconCheck(true);
        updatedComponentInFormState(
          props.selectedControl.id,
          props.selectedControl
        );
      }
    } else if (action === "remove") {
      delete props.selectedControl.style["icon"];
      var jsonForIcons = jsonIcons;
      jsonForIcons = {};
      setjsonForIcons(jsonForIcons);
      setIconCheck(false);
      updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
    }
  }

