import { makeStyles } from '@material-ui/core/styles';
import { useEffect } from 'react';
import { useDispatch, addContexts, useSelector } from "../../../../Redux"
import { Backdrop } from '@material-ui/core';
import zIndex from '@material-ui/core/styles/zIndex';
import { GetStateFromData } from '../../../CustomHooks/CustomHooks';
const useStyles = makeStyles({
    shademain: {
        position: "fixed",
        left: 0,
        top: 0,
        width: "100vw",
        height: "100vh",
        background: "#00000090",
        zIndex: "99"
    },
    microFrontend_Body: {
        position: 'fixed',
        left: '50%',
        top: '50%',
        transform: 'translate(-50% ,-50%   )',
        background: '#fff',
        height: '80vh',
        width: '80vw',
    },
    pickListMF_Parent: {
        height: '100%',
        width: '100%',
    }
});
const PickListMF = (props) => {
    const contexts = GetStateFromData(props.containerId);
    const classes = useStyles();
    const { dataObjectsContent } = contexts.contexts;
    const dataObjectsArray = [];
    const filterData = (dataObjectsContent) => {
        for (let i = 0; i < dataObjectsContent?.length; i++) {
            if (dataObjectsContent[i].Unbounded === "N") {
                dataObjectsArray.push({
                    name: dataObjectsContent[i].treeName,
                    id: dataObjectsContent[i].treeName
                })
            }
            if(dataObjectsContent[i]?.children && dataObjectsContent[i]?.children?.length>0 && dataObjectsContent[i].Unbounded!=="Y"){
                filterData(dataObjectsContent[i]?.children)
            }
        }

    }

    useEffect(() => {
        filterData(dataObjectsContent);
        let modifyProps={};
        const creationProps = {
            "mode": "New",
            "mapFields": dataObjectsArray,
            "ContainerId": "pickListMF_Parent_MDM",  // Id of the container in which MF loads
            "Module": "MDM",
            "prefix_input_placeholder": "'",
            "suffix_input_placeholder": ".value'",
            "Component": "PicklistInstance",
            "InFrame": false,
            "Renderer": "renderPicklistInstance",
            "Callback": props.picklistCallBackMF,
            "cancelCallback":props.picklistCancelCallback,
            "picklist_integration_type":"iform_dynamic"
        }
        if(props?.selectedControl?.style?.picklist_instance_id !==undefined && props?.selectedControl?.style?.picklist_instance_id !==""){
            modifyProps={
                "mode": "Modify",
                "mapFields": dataObjectsArray,
                "ContainerId": "pickListMF_Parent_MDM",  // Id of the container in which MF loads
                "Module": "MDM",
                "prefix_input_placeholder": "'",
                "suffix_input_placeholder": ".value'",
                "Component": "PicklistInstance",
                "InFrame": false,
                "Renderer": "renderPicklistInstance",
                "Callback": props.picklistCallBackMFModify,
                "cancelCallback":props.picklistCancelCallback,
                "picklist_instance_id":props?.selectedControl?.style?.picklist_instance_id,
                "picklist_name":props?.selectedControl?.style?.picklist_name,
                "picklist_integration_type":"iform_dynamic" 
            }
            window.loadMicroFrontend(modifyProps)
           

        }     
        else
        {
            window.loadMicroFrontend(creationProps)
            
        }
        
    },[])
    
    return (
        <Backdrop style={{zIndex:1}} open={props.openMF}
        >
        <div id="pickListMF_Parent_MDM" className={[classes.pickListMF_Parent,"childmf-formb"].join(" ")}>
        </div>
        </Backdrop>
    );
};
export default PickListMF;
